.intro__list {
  list-style: none;
  margin: 0;
  padding-bottom: 5px;
  padding-left: 68px;
  padding-top: 41px;
  position: relative;
}
.intro__head {
  margin: 0;
  font-size: 18px;
  padding-bottom: 16px;
  position: relative;
}
.intro__item {
  height: 92px;
  padding-bottom: 39px;
  position: relative;
}
.intro__item:nth-child(2) {
  padding-bottom: 20px;
}
.intro__item::before {
  width: 80px;
  height: 75px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 39px;
  content: "";
  position: absolute;
  left: -71px;
  top: -30px;
}
.intro__item:first-child::before {
  background-image: url(../../img/icon-house-blue.svg);
}
.intro__item:nth-child(2)::before {
  background-image: url(../../img/icon-gift-blue.svg);
  background-size: 32px;
}
.intro__item:last-child::before {
  background-image: url(../../img/icon-burger-blue.svg);
}
.intro__text {
  margin: 0;
  width: 230px;
}
.intro__text-transfer {
  display: none;
}
@media (min-width: 768px) {
  .intro__list {
    display: flex;
    padding: 0;
    padding-top: 163px;
    padding-bottom: 38px;
    margin-left: 48px;
    margin-right: 47px;
    justify-content: space-between;
  }
  .intro__head {
    font-size: 21px;
    padding-bottom: 23px;
  }
  .intro__item {
    width: 24%;
    text-align: center;
  }
  .intro__item:nth-child(2) {
    order: 3;
  }
  .intro__item:nth-child(2)::before {
    background-size: 77%;
  }
  .intro__item:last-child {
    width: 23%;
  }
  .intro__item::before {
    left: 50%;
    transform: translateX(-50%);
    top: -104px;
    background-size: 100%;
  }
  .intro__text {
    width: auto;
  }
}
@media (min-width: 1200px) {
  .intro__head::before {
    left: 180px;
  }
  .intro__list {
    margin: 0px 71px;
  }
  .intro__item:last-child {
    width: 28%;
  }
  .intro__text-transfer {
    display: block;
  }
}
