.main {
  font-family: "PT Sans", sans-serif;
  font-style: normal;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.main-wrap {
  max-width: 320px;
  margin: 0 auto;
  box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.8);
}
.main-header {
  position: relative;
}
.main-header__btn {
  padding: 14px;
  padding-left: 278px;
}
.main-nav {
  position: relative;
}
.main-nav__list {
  margin: 0;
  padding: 0;
}
.main-nav__item {
  list-style: none;
  height: 54px;
  background-color: #81b3d2;
  color: white;
  text-align: center;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
}
.main-nav__item:hover {
  background-color: #669ec0;
}
.main-nav__item:active {
  background-color: #5496bd;
}
.main-nav__link {
  color: white;
  text-decoration: none;
  font-size: 18px;
  line-height: 55px;
  font-weight: bold;
}
.main-nav__link:active {
  color: black;
  opacity: 0.3;
}
.main-nav__toggle {
  width: 23px;
  height: 22px;
  border: none;
  background-color: transparent;
  background-image: url(../../img/icon-menu-blue.svg);
  background-repeat: no-repeat;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle--close {
  background-image: url(../../img/main-nav__toogle.png);
  position: absolute;
  left: 100%;
  transform: translateX(-100%);
}
.main-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}
.main-header__logo-link {
  width: 100%;
  height: auto;
}
.main-bg {
  width: 100%;
  min-height: 390px;
  background-image: url(../../img/bg-back-mobile@1x.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
.main-bg::after {
  content: '';
  height: 26px;
  width: 100%;
  background-image: url(../../img/bg-intro-triangle-mobile.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  position: absolute;
  left: 0;
  top: 100%;
  transform: translateY(-100%);
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-bg {
    background-image: url(../../img/bg-back-mobile@2x.jpg);
  }
}
.main-bg__img {
  text-align: center;
}
.main-bg__img-link {
  width: 280px;
  padding-top: 20px;
}
.main-bg__img-link--form {
  padding-top: 0px;
}
.main-bg--form {
  min-height: 160px;
  background-position: bottom;
}
.main-bg--photo {
  min-height: 160px;
  background-position: bottom;
}
.main-bg__img--form {
  padding-top: 50px;
}
.main-bg__img--photo {
  padding-top: 50px;
}
/*------------------------------------------------------------------
            MEDIA
----------------------------------------------------------------------------------*/
@media (min-width: 768px) {
  .main-wrap {
    max-width: 768px;
    margin: 0 auto;
    box-shadow: 0px 0px 9px -2px rgba(0, 0, 0, 0.8);
  }
  .form-main {
    padding-bottom: 24px;
  }
